import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm, reset } from "redux-form";
import {
  getDataHadiah,
  hideModal,
  showModal,
} from "../../../actions/datamaster_action.jsx";
import { ShowModalTukarPoin } from "../../../actions/member_action.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { getData, postData } from "../../../config/axios.jsx";
import HeadTukarPoin from "./HeadTukarPoin.jsx";
import ModalTukarPoin from "./Modal/ModalTukarPoin.jsx";
import Loading from 'react-fullscreen-loading';
const maptostate = (state) => {
  return {
    datahadiah: state.datamaster.getDataHadiah,
    hideModal: state.datamaster.modalDialog,
  };
};
class FormTukarPoin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      point: "POINT",
      isLoading: false,
      isLoadingTukarPoin: false,
      columns: [
        {
          dataField: "nama_hadiah",
          text: "Nama Hadiah",
          sort: true,
        },
        {
          dataField: "poin",
          text: "Poin",
        },
        {
          dataField: "stock",
          text: "Stock",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              kode_hadiah: row.kode_hadiah,
              nama_hadiah: row.nama_hadiah,
              poin: row.poin,
            };
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.showDataHadiah(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-exchange-alt"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.getKodeMember = this.getKodeMember.bind(this);
  }
  showDataHadiah(data) {
    let data_member = localStorage.getItem("data_member") || [];
    if (data_member.length === 0) {
      ToastNotification("info", "Kode Member Belum Diisi");
    } else {
      this.props.dispatch(ShowModalTukarPoin(data));
      this.props.dispatch(showModal());
    }
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_member = JSON.parse(localStorage.getItem("data_member") || []);
    let data_post = {
      kode_member: data_member[0].kode_member,
      nama_customer: data_member[0].nama_customer,
      alamat_customer: data_member[0].alamat_customer,
      poin: parseInt(data.poin),
      kode_hadiah: data.kode_hadiah,
      nama_hadiah: data.nama_hadiah,
    };
    postData("trxmember/poin/tukar", data_post)
      .then((res) => {
        this.props.dispatch(reset("ModalHadiah"));
        this.props.dispatch(reset("HeadTukarPoin"));
        ToastNotification("success", res.data.message);
        localStorage.setItem("data_member", JSON.stringify([]));
        this.props.dispatch(reset("HeadFormPoint"));
        document.getElementById("transaksi_poin_nama_member").value = "";
        document.getElementById("transaksi_poin_nama_member").value = "";
        document.getElementById("transaksi_poin_alamat").value = "";
        document.getElementById("transaksi_poin_no_hp").value = "";
        this.setState({
          isLoading: false,
        });
        this.props.dispatch(hideModal());
        getData("member/get/by-kode-member/" +data_member[0].kode_member).then((res) => {
          // console.log(res.data);
          if (res.data.length === 0) {
            ToastNotification('info', 'Kode Member Tersebut Tidak Ada')
            document.getElementById("transaksi_poin_nama_member").value =""
            document.getElementById("transaksi_poin_alamat").value =""
            document.getElementById("transaksi_poin_no_hp").value = ""
            this.setState({
              datatransaksimember: [],
              point: 0,
              isLoading: false
            });
          } else {
            localStorage.setItem("data_member", JSON.stringify(res.data));
            res.data.forEach((item, index) => {
              document.getElementById("kode_member").value =
                item.kode_member;
              document.getElementById("transaksi_poin_nama_member").value =
                item.nama_customer;
              document.getElementById("transaksi_poin_alamat").value =
                item.alamat_customer;
              document.getElementById("transaksi_poin_no_hp").value = item.no_hp;
              // this.props.change("point", item.point);
              this.setState({
                datatransaksimember: [],
                point: item.point || 0,
                isLoading: false
              });
            });
          }
          // console.log(res.data);
        });
      })
      .catch((err) => {
        ToastNotification("info", err.response.data || "Terjadi kesalahan");
        this.setState({
          isLoadingTukarPoin: false,
          isLoading: false
        });
      });
  }
  getKodeMember(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      if(e.kode_member === undefined){
        return false;
      }
      this.setState({
        isLoading: true
      })
      getData("member/get/by-kode-member/" + e.kode_member).then((res) => {
        console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification('info', 'Kode Member Tersebut Tidak Ada')
          document.getElementById("transaksi_poin_nama_member").value =""
          document.getElementById("transaksi_poin_alamat").value =""
          document.getElementById("transaksi_poin_no_hp").value = ""
          this.setState({
            datatransaksimember: [],
            point: 0,
            isLoading: false
          });
        } else {
          localStorage.setItem("data_member", JSON.stringify(res.data));
          res.data.forEach((item, index) => {
            document.getElementById("transaksi_poin_nama_member").value =
              item.nama_customer;
            document.getElementById("transaksi_poin_alamat").value =
              item.alamat_customer;
            document.getElementById("transaksi_poin_no_hp").value = item.no_hp;
            // this.props.change("point", item.point);
            this.setState({
              datatransaksimember: [],
              point: item.point || 0,
              isLoading: false
            });
          });
        }
        // console.log(res.data);
      });
    }, 1500);
  }
  componentDidMount() {
    this.props.dispatch(getDataHadiah());
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Member</Link>
          </li>
          <li className="breadcrumb-item active">Tukar Poin</li>
        </ol>
        <h1 className="page-header">Tukar Poin </h1>
        <Panel>
          <PanelHeader>Tukar Poin</PanelHeader>
          <br />
          {/* Tukar Poin */}
          <div className="container">
            <HeadTukarPoin
              onChange={(e) => this.getKodeMember(e)}
              point={this.state.point}
            />
            <Tabel
              keyField="kode_hadiah"
              exportCsv={true}
              data={this.props.datahadiah}
              columns={this.state.columns}
              textEmpty="Data Transaksi Member Kosong"
            />
          </div>
          <br />
          {/* End Tambah Tukar Poin  */}
          <ModalTukarPoin
            isLoading={this.state.isLoading}
            isEdit={this.state.isEdit}
            title="FormTukar Point"
            isOpen={this.props.hideModal}
            onSubmit={(data) => this.handleSubmit(data)}
          />
        </Panel>
        {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </div>
    );
  }
}

FormTukarPoin = reduxForm({
  form: "FormTukarPoin",
  enableReinitialize: true,
})(FormTukarPoin);
export default connect(maptostate, null)(FormTukarPoin);
