import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { showModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
// import ModalGlobal from "../../../components/helpers/ModalGlobal";
import { ToastNotification } from "../../../components/helpers/notification";
import { Panel } from "../../../components/panel/panel";

// import { getData } from "../../../config/axios";

class HeadTukarPoin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      kondisi_barang: "kosong",
        FormModal: "",
    };
  }

  
  ShowModalTambahPoin() {
    let data_member = localStorage.getItem("data_member") || [];
    if (data_member.length === 0) {
      ToastNotification("info", "Kode Member Belum Diisi");
    } else {
      this.props.dispatch(showModal());
      this.setState({
        FormModal: "ShowModalTambahPoin",
      });
    }
  }
 
  render() {
    return (
      <Panel>
        <div className="row">
          <div className="col-8">
            <div className="row">
              <div className="col-lg-6">
                <Field
                  id="kode_member"
                  onChange={()=> this.props.getKodeMember}
                  name="kode_member"
                  component={ReanderField}
                  type="text"
                  label="Kode Member"
                  placeholder="Masukan Kode Member"
                />
              </div>
              <div className="col-lg-6">
                <Field
                  id="transaksi_poin_nama_member"
                  name="nama_member"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="Nama Member"
                  placeholder="Masukan Nama Member"
                />
              </div>
            </div>
          </div>
          <div className="col-4 text-center">
            <> &nbsp; </>
            <h1
              style={
                this.props.point === "POINT"
                  ? {
                      fontSize: "4.875rem",
                      position: "absolute",
                      left: "56px",
                      top: "25px",
                    }
                  : {
                      fontSize: "4.875rem",
                      position: "absolute",
                      left: "110px",
                      top: "25px",
                    }
              }
            >
              {" "}
              {this.props.point}{" "}
            </h1>
          </div>
          <div className="col-8">
            <div className="row">
              <div className="col-lg-6">
                <Field
                  id="transaksi_poin_alamat"
                  name="alamat"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="Alamat"
                  placeholder="Masukan Alamat"
                />
              </div>
              <div className="col-lg-6">
                <Field
                  id="transaksi_poin_no_hp"
                  name="no_hp"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="No Hp"
                  placeholder="Masukan No Hp"
                />
              </div>
            </div>
          </div>
          <div className="col-4 text-center">
          </div>

        </div>
       
      </Panel>
    );
  }
}

HeadTukarPoin = reduxForm({
  form: "HeadTukarPoin",
  enableReinitialize: true,
})(HeadTukarPoin);
export default connect()(HeadTukarPoin);
