import React, { Component } from "react";
import { connect } from "react-redux";
import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../../actions/datamaster_action";
import {
  ReanderField,
} from "../../../../components/helpers/field";
import ValidasiMasterDataHadiah from "../../../../Validasi/ValidasiMasterDataHadiah.jsx";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  if (state.datamember.ShowModalTukarPoin !== null) {
    return {
      initialValues: {
        nama_hadiah: state.datamember.ShowModalTukarPoin.nama_hadiah,
        poin: state.datamember.ShowModalTukarPoin.poin,
        kode_hadiah: state.datamember.ShowModalTukarPoin.kode_hadiah,
      },
    };
  }
};
class ModalTukarPoin extends Component {
  state = {};
  render() {
    return (
      <Modal
        isOpen={this.props.isOpen}
        backdrop="static"
        keyboard={false}
        toggle={() => this.props.dispatch(hideModal())}
      >
        <ModalHeader> {this.props.title} </ModalHeader>
        <form onSubmit={this.props.handleSubmit} autoComplete="off">
          <ModalBody>
            <Field
              name="kode_hadiah"
              component={ReanderField}
              type="text"
              readOnly={true}
              label=" Kode Hadiah"
              placeholder="Masukan  Kode Hadiah"
            />
            <Field
              name="nama_hadiah"
              component={ReanderField}
              type="text"
              readOnly={true}
              label=" Nama Hadiah"
              placeholder="Masukan  Nama Hadiah"
            />
            <Field
              name="poin"
              component={ReanderField}
              type="number"
              readOnly={true}
              label="Poin"
              placeholder="Masukan Poin"
            />
          </ModalBody>
          <ModalFooter>
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            <button
              className="btn btn-primary"
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </ModalFooter>
          {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
        </form>
      </Modal>
    );
  }
}

ModalTukarPoin = reduxForm({
  form: "ModalHadiah",
  enableReinitialize: true,
  validate: ValidasiMasterDataHadiah,
})(ModalTukarPoin);
export default connect(maptostate, null)(ModalTukarPoin);
